// Imports
import Vue from 'vue'
import Router from 'vue-router'
import { trailingSlash } from '@/util/helpers'
import {
  layout,
  route,
} from '@/util/routes'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return { selector: to.hash }
    if (savedPosition) return savedPosition

    return { x: 0, y: 0 }
  },
  routes: [
    layout('Default', [
      route('Dashboard'),

      // Pages
      route('UserProfile', null, 'components/profile'),
      // Components
      route('Notifications', null, 'components/notifications'),
      route('Icons', null, 'components/icons'),
      route('Typography', null, 'components/typography'),
      route('Orders/Reguler/Reguler', null, 'orders/reguler'),
      route('Orders/Express/Express', null, 'orders/express'),
      route('Orders/Reguler/History', null, 'orders/history/reguler'),
      route('Orders/Express/History', null, 'orders/history/express'),
      route('Drivers/Reguler', null, 'drivers/reguler'),
      route('Drivers/Express', null, 'drivers/express'),
      route('Drivers/Wallets', null, 'drivers/wallet'),
      route('Admins/Users', null, 'admins/users'),
      // route('Admins/DeliveryFee', null, 'admins/delivery-fee'),
      route('Admins/Chats', null, 'admins/chats'),
      route('Admins/AppVersion', null, 'admins/app-version'),
      route('Admins/SpecialRegion', null, '/admins/special-region'),
      route('Admins/Notification', null, '/admins/notification'),
      route('Admins/Banner', null, '/admins/banner'),

      // Tables
      route('Regular Tables', null, 'tables'),

      // Maps
      route('Google Maps', null, 'admins/driver-loc'),
    ]),
    {
      path: '/auth/login',
      name: 'login',
      component: () => import('../views/Login.vue'),
    },
  ],
})

router.beforeEach((to, from, next) => {
  return to.path.endsWith('/') ? next() : next(trailingSlash(to.path))
})

export default router
